@import "../../global.scss";

.intro{
    background-color: #fff;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex: 0.5;  // 50 % left and 50% right thase flex grow:0.5 , flex-shrink :1
        // background-color: yellow; for understanding mate
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right; // aanathi thodu perfect thase background

            @include mobile{
                align-items:flex-start;
            }

            img{
                width: 90%; 

                @include mobile{
                    // width: 50%; for assest
                    width: 86%;
                    margin-top: -150px;
                }
            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;
        // background-color: red;for understanding mate
        
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left:0;
                align-items: center;
            }

            h1{
                font-size: 60px;
                margin: 10px 0;

                @include mobile{
                    font-size: 40px;
                }
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;

                @include mobile{
                    font-size: 20px;
                }

                span{
                    font-size: inherit;
                    color: crimson;
                }

                .ityped-cursor{   // aastyle cursorni 6 typed na class inbuiltj 6
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }

        a{
            position: absolute;
            bottom: 10px;
            left: 40%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink{
                100%{
                    opacity: 0;
                }
            }
        }
    }
}



